/* Style for the popup container */
.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 9999;
  padding: 30px;
  width: 70%;
  max-width: 600px;
  text-align: left;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  animation: popupFadeIn 0.3s ease-in-out forwards;
  overflow-y: auto; /* Enable vertical scrollbar */
  max-height: 80vh; /* Set maximum height for the popup */
}

/* Animation for fading in */
@keyframes popupFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Style for the close button */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 24px;
  color: #777;
  transition: color 0.3s ease;
}

.close-button:hover {
  color: #333;
}

/* Style for the heading */
.popup-content h2 {
  font-size: 28px;
  color: #333;
  margin-bottom: 20px;
}

/* Style for the order details */
.popup-content p {
  font-size: 16px;
  color: #555;
  margin-bottom: 12px;
}

/* Divider for order details */
.popup-content p:not(:last-child) {
  border-bottom: 1px solid #ccc;
  padding-bottom: 8px;
}

/* Animation for details */
.popup-content p {
  animation: fadeIn 0.5s ease-in-out;
  opacity: 1;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Style for the status filter select */
.status-filter {
  padding: 10px;
  border: 1px solid #aaa;
  border-radius: 6px;
  font-size: 16px;
  outline: none;
  cursor: pointer;
  transition: border-color 0.3s ease;
  background-color: #f8f8f8;
  color: #333;
  animation: selectFadeIn 0.5s ease forwards;
}

/* Hover effect for the select element */
.status-filter:hover {
  border-color: #007bff;
}

/* Style for select options */
.status-filter option {
  background-color: #fff;
  color: #333;
  font-size: 16px;
}

/* Style for selected option */
.status-filter option:checked {
  background-color: #a6a6a6;
  color: #fff;
}

/* Animation for fading in */
@keyframes selectFadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
