/* Styles for loading spinner and page content */
.loading {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.7); /* Adjust opacity as needed */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999; /* Ensure loading spinner appears above other elements */
    width: 100%;
    height: 100%;
  }
  
  /* Hide the loading spinner by default */
  .loading.hidden {
    display: none;
  }
  
  /* Styles for the page content */
  .page-content {
    opacity: 1; /* Ensure the content is visible by default */
    transition: opacity 0.2s ease-in-out; /* Add a smooth transition effect */
  }
  
  /* Hide the page content when the loading spinner is active */
  .page-content.hidden {
    pointer-events: none; /* Disable interactions with hidden content */
    opacity: 0;
  }
  
  /* Styles for the content specific to /verification, /SignUp, /Login */
  .route-content {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.7); /* Adjust opacity as needed */
    z-index: 999; /* Ensure it appears above other content but below loading spinner */
  }
  