/* Verification.css */

.verification-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
    font-family: Arial, sans-serif;
  }
  
  .account-exists,
  .success {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .account-exists p,
  .success p {
    margin-bottom: 15px;
  }
  
  .success input[type='text'] {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  .success button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .success button:hover {
    background-color: #0056b3;
  }
  h4{
    
    font-size: 25px;
font-family: 'Segoe UI';
margin: auto;
margin-bottom: 50px;
color: rgb(186, 186, 186);
text-shadow: 3px 3px 6px rgba(0,0,0,0.8);
  }
  
  .passwor-error {
    color: #f4ecec;
    font-size: 15px;
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 5px;
    margin-right: 218px;
    display: block;
  }
  
