.cart-page-styles {
  max-width: 900px;
  margin: 0 auto;
}
  
  .box {
    padding: 20px;
    margin-bottom: 20px;
    background-color: #f4f4f4;
    border-radius: 5px;
  }
  
  /* Existing CartPage Styles */
  .cart-page {
    padding: 30px;
    font-size: 14px;
    font-family: Arial, sans-serif;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
