  }
  
  .cart-item h1 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
  }
  
  .cart-item p:first-child {
    font-weight: bold;
  }
  
  .cart-item p:last-child {
    margin-bottom: 0;
  }
  
  .cart-item-details {
    flex: 1;
  }
  
  .selected-data {
    margin-top: 10px;
    font-weight: bold;
