/* Style for the remove button */
.remove-button {
  padding: 6px 12px; /* Adjust padding to make it smaller */
  background-color: #ff4f00;
  color: #fff;
  margin-right: 20px !important;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: block;
  margin: 0 auto; /* Centers the button */
}

/* Hover effect for the remove button */
.remove-button:hover {
  background-color: #c73f00;
}

.empty123 {
  /* Your existing styles */
  font-family: Arial, sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #ff4f00; /* Background color */
  border: 1px solid #ff4f00; /* Border color */
  padding: 12px 20px;
  border-radius: 8px;
  text-align: center;
  width: 220px;
  margin: 0 auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Adds a subtle shadow */
  animation: sadAnimation 1.5s ease-in-out infinite;
}

@keyframes sadAnimation {
  0%, 100% {
    transform: translateY(0) rotate(0);
    opacity: 1;
  }
  50% {
    transform: translateY(-8px);

  }
}


.clear-cart-button {
  padding: 10px;
  border: none;
  background-color: #f74e00;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 6px;
  width: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.fixed-button {
  position: fixed !important;
  bottom: 90px !important;
  right: 115px;
  z-index: 999;
}

.fixed-button:hover{
  background-color: #d04200;
}


.empty-cart-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(135deg, #ff4f00, #ffa2b1);
  border: 0px solid #ff4f00;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 18px;
  color: #333333;
  max-width: 80%;
  z-index: 9999;
  animation: colorShift 5s ease infinite alternate;
}

/* Keyframes for slideDown animation */
@keyframes slideDown {
  0% {
    transform: translate(-50%, -70%);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}
