.nav-container {
  background: black;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 10px;
  }
  
  .nav-list {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  }
  
  .nav-link {
  text-decoration: none;
  color: #fff;
  }
  

  
  .language-button {
  background: transparent;
  color: #fff;
  border: none;
  cursor: pointer;
  font-family: system-ui;
  margin-left: 1px;
  margin-bottom: 5px;
  }
  
  .nav-right {
  display: flex;
  align-items: center;
  }
  
  .nav-button {
  background: transparent;
  color: #fff;
  border: none;
  cursor: pointer;
  }
  
  .navicon {
  margin-bottom: 3.5px;
  }
  

  
  .chart {
  display: flex;
  align-items: flex-start;
  margin-left: 100px;
  }
  
  .circle {
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    top: 9px;
    right: -10px;
    font-size: 27px;
  }

  .circle:hover .item-count {
    transform: scale(1.1);
  }
  
  .item-count {
    position: absolute;
    top: 2.9px;
    right: -30px;
    background-color: #ff4f00;
    color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    font-weight: bold;
    transition: transform 0.3s ease-in-out;
  }

  
  .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  font-size: 3rem;
  }
  
  .cartname {
  font-family: system-ui;
  margin-top: 8px;
  margin-left: 5px;
  }
  
  
  
  .logo-image {
  width: 28vh;
  height: auto;
  margin-top: -91px;
  margin-right: 0px;
  }
  

  
  .nav-middle {
  display: flex;
  align-items: center;
  margin-top: 10px;
  }
  
  .nav-buttonn {
  background: transparent;
  color: #fff;
  border: none;
  cursor: pointer;
  position: initial;
  font-family: 'Franklin Gothic Medium';
  font-size: 18px;
  margin-left: 0px;
  margin-right: 15px;
  }
  
  .nav-buttonn:hover {
  color: darkred;
  }
  
  .nav-button {
    
      background-size: 85.5% 85.5% !important;
      transition: transform 0.3s;
      background-repeat: no-repeat;
    
    
  }
  
  .nav-button:hover {
  transform: scale(1.1);
  }
  

  
  .nav-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 68%;
  
  }
  

  
  .offer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16vh;
  }
  
  .offer-section {
  width: 11%;
  height: auto;
  display: block;
  margin: 0 auto;
  }
  
  
  .photo-slider {
  display: flex;
  align-items: center;
  }
  
  .photo-slider button {
  padding: 10px 20px;
  background-color: #0073e6;
  color: white;
  border: none;
  cursor: pointer;
  }
  
  .photo-slider img {
  max-width: 100%;
  max-height: 400px;
  }
  
  .image1 {
  width: 63vh;
  border-radius: 27px;
  }
  

  
  .product-card {
  border: 2px solid var(--toastify-color-light);
  border-radius: 10px;
  margin: 10px;
  padding: 20px;
  text-align: center;
  background-color: var(--toastify-toast-background);
  transition: transform 0.2s;
  box-shadow: 0px 7px 9px rgba(0, 0, 0, 0.1);
  }
  
  .product-card:hover {
  transform: translateY(-5px);
  }
  

  
  .product-card img {
  width: 176px;
  aspect-ratio: 176/176;
  box-sizing: border-box;
  border: 0 solid #e5e7eb;
  display: block;
  max-width: 100%;
  height: auto;
  color: transparent;
  }
  
  .product-card h2 {
  font-size: 1.5rem;
  margin: 10px 0;
  color: var(--main-color);
  }
  
  .product-card p {
  font-size: 0.93rem;
  color: var(--toastify-text-color-light);
  margin: 5px 0;
  }
  
  .product-card button {
  margin-left: 223px;
  cursor: pointer;
  margin-bottom: 10px;
  padding: 10px 20px;
  background: transparent;
  color: #0073e6;
  border: none;
  border-radius: 5px;
  }
  
  .product-card button:hover {
  background: transparent;
  color: #ff1900;
  }
  

  
  .sandwich-container {
  text-align: center;
  }
  
  .sandwich-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  justify-content: center;
  max-width: 1400px;
  margin: 0 auto;
  }
  
  .sus-section{
  width: 15%;
  height: auto;
  display: block;
  margin: 0 auto;
  }
  
  .apt-section{
  width: 22%;
  height: auto;
  display: block;
  margin: 0 auto;
  }
  
  .desserts-section{
  width: 19%;
  height: auto;
  display: block;
  margin: 0 auto;
  }
  
  .drinks-section{
  width: 15%;
  height: auto;
  display: block;
  margin: 0 auto;
  }
  

  
  .scroll-container {
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  }
  
  .stop-scrolling {
  scroll-snap-align: start;
  }
  
  .stop-scrolling img {
  width: 100%;
  height: auto;
  }
  

  
  .scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: white;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 62px;
  cursor: pointer;
  margin-bottom: 65px;
  margin-right: 45px;
  z-index: 1000;
  }

  
  @media(max-width: 768px) {
  

  
  .nav-container {
  flex-direction: column;
  }
  
  .nav-list {
  flex-direction: column;
  }
  
  .offer-section{
    width: 150px;
  }

  .image1{

    width: 60vh;

  }

  .burger-section{
    width: 349px;
    margin-right: 9px;
  }

  .apt-section{
    width: 260px !important;
  }

  .sus-section{

    width: 196px !important;

  }

  .kids-section{

    width: 257px !important;

  }

  .desserts-section{

    width: 229px !important;

  }

  .drinks-section{

    width: 186px !important;
    margin-right: 100px;
  }

  
  .logo-image {
  width: 45vw;
  margin-top: -30px;
  }
  

  .nav-button{
    margin-right: -90px;
    width: 12vh !important;
    margin-left: -44px;
    
  }

  #logo-image2 {
    display: none;
    
    
  }
  

  
  .nav-middle {
  flex-direction: column;
  }
  

  
  .product-card {
  width: 90%;
  }
  

  
  .sandwich-list {
  grid-template-columns: 1fr;
  }
  
  .sus-section,
  .apt-section,
  .desserts-section,
  .drinks-section {
  width: 80%;
  }
  
  }
  
  @media(min-width: 769px) and (max-width: 1024px) {
  

  
  .nav-container {
  padding: 20px;
  }
  

  
  .logo-image {
  width: 40vw;
  }
  

  
  .sus-section,
  .apt-section,
  .desserts-section,
  .drinks-section {
  width: 30%;
  }
  
  }
  
  @media(min-width: 1025px) {
  

  
  .nav-container {
  padding: 10px;
  }
  
 
  
  }

