.logout-button-slide {
  position: absolute;
  top: 140px; /* Adjust the top position as needed */
  right: 20px; /* Adjust the right position as needed */
  z-index: 100;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);

}

.logout-button-slide button {
  background-color: #c00000;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 8px 12px;
  margin-top: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.logout-button-slide button:hover {
  background-color: #a00000;
}

/* Optional: Animation for sliding effect */
.logout-button-slide {
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.logout-button-slide.slide-up {
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-20px);
    opacity: 0;
  }
}

