body {
    margin: 0;
    overflow-x: hidden;
}

.footer {
    background: #000;
    padding: 30px;
    font-family: 'Play',Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    text-align: center;
}

.footer .row {
    width: 100%;
    display: inline-block;
    padding: 0.8% 0;
    color: gray;
    font-size: 0.8em;
}

.footer .row a {
    text-decoration: none;
    color: gray;
    transition: 0.5s;
    vertical-align: middle;
}

.footer .row a i {
    font-size: 2em;
    margin: 10px; 
}

.footer .row a:hover {
    color: #fff;
}

.footer .row ul {
    width: 100%;
}

.footer .row ul li {
    display: inline-block;
    margin: 0 30px;
}

@media (max-width: 720px) {
    .footer {
        text-align: left;
        padding: 5%;
    }

    .footer .row ul li {
        display: block;
        margin: 10px 0;
        text-align: left;
    }

    .footer .row a i {
        margin: 0 3%;
    }

    .logo-image22{
        margin-top: 13000px !important;
        margin-left: -40000px !important;
    }

    
}

