.size-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 97px;
}

.size-options label {
  position: relative;
  display: inline-block;
  margin-right: 100px;
  transform: translateY(-240%);

}
.size-options input[type="radio"] {
  display: none;
  transform: translateY(-70%);
}


.size-options span.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 40px;
  border: 1px solid #ff4f00;
  background:  #ff4f00 ;
  color: #fff;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.size-options span.icon::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%);
  border-radius: 4px;
  z-index: -1;
  transition: all 0.3s ease;
}

.size-options input[type="radio"]:checked + span.icon {
  transform: translateY(2px);

}

.size-options input[type="radio"]:checked + span.icon:before {
  background-color: rgb(27, 27, 27);
}



.size-options span.text {

  box-shadow: none;
