/* Extra Ingredients Styling */
.ingredients-box {
  padding-top: 13px;
  border-radius: 5px;
  background-color: rgba(47, 47, 47, 0);
  height: 126px;
  align-items:center;
  margin-top: -150px;
  transform: translateY(130%) !important;

}

.extra-ingredients {
  display: flex;
  justify-content: space-between;
  width: 10%;
  transform: translateX(142%) !important;
  margin-left: -115px;


}

.extra-ingredients h3 {
  font-size: 18px;
  margin-bottom: 10px;
  width: 100%;
  
}

.extra-ingredients label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  white-space: wrap;
  
}

.extra-ingredients input[type="checkbox"] {
  display:block;
}

.extra-ingredients input[type="checkbox"] + label::before {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 2px solid #000;
  margin-right: 5px;
  cursor: pointer;
}

.extra-ingredients input[type="checkbox"]:checked + label::before {
  background-color: #000;
}

.extra-ingredients label {
  cursor: pointer;
  color: #ff5100;
}

.extra-ingredients label::after {
  content: attr(data-price);
  margin-left: 8px;
  color: #666;
}

/* Separate top and bottom rows */
.extra-ingredients .top-row,
.extra-ingredients .bottom-row {
  display: flex;
  gap: 1px; /* Adjust the gap between checkboxes */
}

.extra-ingredients .top-row label,
.extra-ingredients .bottom-row label {
  flex: 1;
  white-space: nowrap;
  
}

.extra-ingredients .top-row{
  transform: translateY(170%) !important;
  margin-left: 655px;

}


