
.background-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #0452b1, #e0f0f2, #0452b1);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
}

.logo-imageee{
  position: absolute;
  top: 1%;
  left: 43.5%;
  transform: translate(-50%, -50%);
  max-width: 200px;
  margin: 20px auto; /* Adjust the margin as needed */
  width: 200px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgb(188, 209, 236);
  overflow: hidden;
  text-align: center;
}


.checkout-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 600px;
  margin: 20px auto; /* Adjust the margin as needed */
  width: 600px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: center;
}

/* Pseudo-element for background animation */
.checkout-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #d4e4f7, #bce4f1, #d4e4f7);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
  z-index: -1;
}

/* Animation */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Headings and other styles for the checkout content */
.checkout-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.checkout-container h3 {
  font-size: 18px;
  margin-bottom: 12px;
  color: #444;
}

.order-summary {
  margin-bottom: 20px;
}

.order-summary p {
  font-size: 16px;
  margin: 8px 0;
}

.payment-methods {
  margin-bottom: 20px;
}

.payment-methods button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  color: #fff;
  background-color: #007bff;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

.payment-methods button.active {
  background-color: #0056b3;
}
