
/* Style for the card */
.product-card {
    position: relative; 
  }
  
  .CartBtn {
    width: 131px;
    height: 31px;
    border-radius: 12px;
    border: none;
    background-color: #FFB100!important;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition-duration: 0.5s;
    overflow: hidden;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.103);
    position: absolute; 
    right: 20px; 
    bottom: 13px; 
    z-index: 3; 
  }
  
  .IconContainer {
    position: absolute;
    left: -50px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    z-index: 2;
    transition-duration: 0.5s;
  }
  
  .text {
    height: 3px;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(0, 0, 0)!important;
    z-index: 1;
    transition-duration: 0.5s;
    font-size: 1.04em;
    font-weight: 600;
  }
  
  .CartBtn:hover .IconContainer {
    transform: translateX(58px);
    border-radius: 40px;
    transition-duration: 0.5s;
  }
  
  .CartBtn:hover .text {
    transform: translate(10px, 0);
    transition-duration: 0.5s;
  }
  
  .CartBtn:active {
    transform: scale(0.95);
    transition-duration: 0.5s;
  }